import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ModalController, ViewController, LoadingController, AlertController, Searchbar, ToastController, Modal } from 'ionic-angular';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { SoapService } from '../soap.service';
 
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sender_id , oneSignalAppId, sc_code } from '../../config';
import { OneSignal } from '@ionic-native/onesignal'; 
/**
 * Generated class for the AddQuestionIpcContactPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-question-ipc-contact',
  providers: [SoapService],
  templateUrl: 'add-question-ipc-contact.html',
})
export class AddQuestionIpcContactPage {

  public addQuestionForm: FormGroup;
  loading:any;
  userdataTPK:any;
  userdataIPCContact:any;
  direktoratList: Array<any>=[];
  kategoriList:Array<any>=[];
  modul:any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private _fb: FormBuilder,
    public popoverCtrl: PopoverController,    
    public viewCtrl: ViewController, 
    public soapService:SoapService,
    public loadingCtrl:LoadingController,
    public storage:Storage,
    public alertCtrl:AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController,
    public oneSignal: OneSignal
  ) {    
    this.modul = navParams.get('modul');
    oneSignal.startInit(oneSignalAppId, sender_id);
    oneSignal.endInit();â€‹
    this.addQuestionForm = this._fb.group({
      fcDirektorat: ['', Validators.compose([
        // Validators.required
      ])],
      fcKategori: ['', Validators.compose([
        Validators.required
      ])],
      fcJudul: ['', Validators.compose([
        Validators.required
      ])],
      fcDeskripsi: ['', Validators.compose([
        Validators.required
      ])]          
    });
  }
 
  ionViewWillEnter() {
    this.storage.get('userdataTPK').then((val) => {
      this.userdataTPK = val;
    });
    this.storage.get('userdataIPCContact').then((val) => {
      this.userdataIPCContact = val;    
      this.getDataDirektorat();
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddQuestionIpcContactPage');
  }

  getDataDirektorat() {    
    this.loadingPresent();
    this.soapService
    .post(api_base_url,'eoffice_data_direktorat_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass,
        modul : this.modul
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      // console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.direktoratList = []; 
        if (responData['data'].length > 0) {
          for (let i=0; i < responData['data'].length; i++) {
            this.direktoratList.push(responData['data'][i]);
          }                        
        }       
        this.getDataKategori();

        //console.log(this.direktoratList);       
      } else {        
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();          
        this.loadingDismiss();  
      }            
      
    })
    .catch(error => {
      //console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();  
      this.loadingDismiss();
    });
  }

  getDataKategori() {  
    // let loading = this.loadingCtrl.create({
    //   spinner: 'dots',
    //   content: "Mohon Tunggu...",       
    //   cssClass: 'transparent',
    //   //dismissOnPageChange:true
    // });
    // loading.present();
    this.soapService
    .post(api_base_url,'eoffice_data_kategori_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass,        
        // id_dir: this.addQuestionForm.controls.fcDirektorat.value
        id_dir: this.direktoratList[0]['ID_DIR']
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      //console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.kategoriList = []; 
        if (responData['data'].length > 0) {
          for (let i=0; i < responData['data'].length; i++) {
            this.kategoriList.push(responData['data'][i]);
          }                        
        }            
        //console.log(this.kategoriList);       
      } else {        
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();  
      } 
      // loading.dismiss();     
      this.loadingDismiss();
    })
    .catch(error => {
      //console.log(error);
      let alert = this.alertCtrl.create({
        title: '',
        subTitle: 'Gagal mendapatkan data kategori (2)',
        buttons: ['OK']
      });
      alert.present();      
      // loading.dismiss();
      this.loadingDismiss();
    });
  }

  sendData() {
    this.loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    this.loading.present();
    this.soapService
   // .post(api_base_url,'eoffice_bypass_wso',{fStream:JSON.stringify(
    .post(api_base_url,'eoffice_pengajuan_ipcc',{fStream:JSON.stringify( 
      {
        // sc_type: 'izincuti',
        // sc_code: sc_code,        
        // data : {
        //   person_id : '5409', 
        //   atas : this.batasAtas,
        //   bawah : this.batasBawah
        // }        
        usernameEDI : api_user, 
        passwordEDI : api_pass,        
        from_nipp		: this.userdataTPK['data']['NIPP'],
        // to_nipp			: this.addQuestionForm.controls.fcDirektorat.value,
        to_nipp			: this.direktoratList[0]['ID_DIR'],
        id_kategori	: this.addQuestionForm.controls.fcKategori.value,
        judul			  : this.addQuestionForm.controls.fcJudul.value,
        isi			    : this.addQuestionForm.controls.fcDeskripsi.value,
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      //console.log(responData);
      var title = '';
      if (this.modul == 'hr_contact') {
        title = 'HR Contact';
      } else if (this.modul == 'helpdesk') {
        title = 'Helpdesk';
      }
      if (responData['rcmsg'] == "SUCCESS") {   
        let toast = this.toastCtrl.create({
          message: 'Pengajuan berhasil',
          duration: 3000,
          position: 'bottom'
        });
        toast.present().then(() => {
          this.soapService
          .post(api_base_url,'eoffice_notif_imove_nipp',{fStream:JSON.stringify(
            {
              usernameEDI : api_user, 
              passwordEDI : api_pass,
              nipp		: [],
              data		: {
                "res": "IpcContactQuestionListPage"
              },
              content	: {
                "en": "Permasalahan Baru Dari " + this.userdataTPK['data']['NAMA']
              },
              heading : {
                "en": title
              },
              id_kategori: this.addQuestionForm.controls.fcKategori.value
            }
          
          )}).then(result => {
            let hasil = JSON.parse(String(result));
            //console.log("push notif : " + JSON.stringify(hasil));            
            this.navCtrl.pop();
          }).catch(error => {
              
            //console.log(error);            
            this.navCtrl.pop();
              
          });	
          this.navCtrl.pop();
        });        
      } else {
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();   
      } 
      this.loading.dismiss();
    })
    .catch(error => {
      //console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();    
    });
    this.loading.dismiss();
  }

  cancel() {
    this.viewCtrl.dismiss();
  }


  loadingPresent() {
    this.loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    this.loading.present();
  }

  loadingDismiss() {
    if(this.loading.present()) {
      this.loading.dismiss(); 
    }
  }

}



